import { Grid } from '@material-ui/core';
import { Autocomplete, DatePicker, RadioGroup } from 'form';
import React, { ReactElement, useEffect } from 'react';
import { useFormContext, useWatch } from 'react-hook-form';

import { livingCityTemp } from './tempValues';

export const PersonalInfo = (): ReactElement => {
  const { setValue } = useFormContext();
  const relocation = useWatch({
    name: 'relocation'
  });

  useEffect(() => {
    if (relocation === 'impossible' || relocation === null) {
      setValue('movingCityId', null);
    }
  }, [relocation, setValue]);

  return (
    <>
      <Grid item xs={12} md={6}>
        <DatePicker
          name="birthDate"
          label=" "
          required
          views={['year', 'month', 'date']}
          openTo="year"
          disableFuture
          fullWidth
        />
      </Grid>

      <Grid item xs={12} md={6}>
        <RadioGroup
          name="gender"
          label=""
          radioList={[
            { value: 'male', label: '' },
            { value: 'female', label: '' }
          ]}
          formControlProps={{
            fullWidth: true,
            required: true
          }}
          row
        />
      </Grid>

      <Grid item xs={12} md={6}>
        <RadioGroup
          name="businessTripReady"
          label="  "
          radioList={[
            { value: 'never', label: '' },
            { value: 'ready', label: '' },
            { value: 'sometimes', label: '' }
          ]}
          formControlProps={{
            fullWidth: true
          }}
          row
        />
      </Grid>

      <Grid item xs={12} md={6}>
        <RadioGroup
          name="relocation"
          label=""
          radioList={[
            { value: 'impossible', label: '' },
            { value: 'possible', label: '' },
            { value: 'desirable', label: '' }
          ]}
          formControlProps={{
            fullWidth: true
          }}
          row
        />
      </Grid>

      {relocation && relocation !== 'impossible' && (
        <Grid item xs={12}>
          <Autocomplete
            options={livingCityTemp}
            name="movingCityId"
            label=" "
            fullWidth
          />
        </Grid>
      )}
    </>
  );
};
